
import { defineComponent } from "vue";
import Pagination from "@/shared/components/Pagination/Pagination.vue";
import VueDatePicker from "@vuepic/vue-datepicker";
import { PAGE_SIZE } from "@/shared/constants/common.const";
import { IbActivityLogRecordForBackend, RPT204000311_REQ, RPT204000311_RES } from "@/shared/type/RPT202001SS15.type"
import "@vuepic/vue-datepicker/dist/main.css";
import { ExportExcel } from "@/shared/services/ExportExcelService";
import { ExportSheet } from "@/shared/type/ExportExcel.type";

const excel = new ExportExcel()

export default defineComponent({
    name: "rpt202001ss15",
    components: { Pagination, VueDatePicker },

    data() {
        return {
            date: '' as unknown,
            header: [
                { columnName: "No." },
                { columnName: "User Name" },
                { columnName: "User Role" },
                { columnName: "Job Title" },
                { columnName: "Email ID" },
                { columnName: "Phone Number" },
                { columnName: "Branch" },
                { columnName: "Address" },
                { columnName: "Activity log type" },
                { columnName: "First Login" },
                { columnName: "Last Login" },
            ],
            table: [] as IbActivityLogRecordForBackend[],
            totalItems: 0,
            currentPage: 1,
            pageSize: PAGE_SIZE,
            startingIndex: 1,
            isResetPage: false,
        };
    },

    computed: {
        formattedDate(): string {
            return this.date ? this.$datetime.formatVueDatePicker(this.date as Date) : ''
        },
    },

    mounted() {
        this.getIbActivityLogRecordForBackendDetail(false);
    },

    methods: {
        async getIbActivityLogRecordForBackendDetail(isSearch: boolean) {
            const req = {
                systemDate: this.date ? this.$datetime.convertDateToString(this.date as Date) : '',
                pageSize: this.pageSize,
                pageNumber: isSearch ? 0 : this.currentPage
            } as RPT204000311_REQ;

            if (isSearch) {
                this.currentPage = 1
                this.isResetPage = true;
            }

            const res = await this.$request.getRequest("RPT20400311", req, false) as RPT204000311_RES;

            this.table = res.body.list
            this.totalItems = res.body.totalCount
        },

        async exportToExcel() {
            const customHeader = [
                'User Name',
                'User Role',
                'Job Title',
                'Email ID',
                'Phone Number',
                'Branch',
                'Branch Address',
                'Activity log type',
                'First Login',
                'Last Login'
            ]

            const reqBody = {
                systemDate: this.date ? this.$datetime.convertDateToString(this.date as Date) : '',
                pageSize: this.totalItems,
                pageNumber: 0,
            }
            const res = await this.$request.getRequest("RPT20400311", reqBody, false) as RPT204000311_RES;

            const tableForExport = this.onMappingTableData(res.body.list)

			const dataForExport: ExportSheet = {
				data: tableForExport,
				columnHeader: [customHeader],
				sheetName: 'IB Activity Log Bankend user'
			}
			excel.exportSheet([dataForExport], 'IB Activity Log Record Report (backend user)')
        },

        onMappingTableData(lists: IbActivityLogRecordForBackend[]): IbActivityLogRecordForBackend[] {
            return lists.map((item: IbActivityLogRecordForBackend) => ({
                roleName: item.roleName,
                roleID: item.roleID,
                jobTitle: item.jobTitle,
                email: item.email,
                cellPhoneNo: this.$format.formatPhoneNumber(item.cellPhoneNo),
                transactionBranchName: item.transactionBranchName,
                branchAddress: item.branchAddress,
                activities: item.activities,
                firstLoginDate: this.$datetime.formatStringToStringDate(item.firstLoginDate),
                lastLoginDate: this.$datetime.formatStringToStringDate(item.lastLoginDate)
            }));
        },

        handlePagination(pageNumber: number) {
            this.isResetPage = false;
            this.currentPage = pageNumber;
            this.getIbActivityLogRecordForBackendDetail(false);
        },

        handleStartingIndex(startingIndex: number) {
            this.startingIndex = startingIndex;
        },

        handleReset() {
            this.date = '' as unknown;
            this.currentPage = 1;
            this.isResetPage = true;
            this.getIbActivityLogRecordForBackendDetail(false);
        },
    },
});
